import React, { useEffect } from "react";

import Navbar from "../components/navbar";
import Sidebar from "../components/sidebar";
import Footer from "../components/footer";
import CardExtrato from "../components/cardextrato";
import Theme from "../theme";

export default function Extrato(){

    Theme();

    useEffect(() => {
        const token = localStorage.getItem('token');
        const expires = localStorage.getItem('expires');
        const now = Date();
        if(!token || expires < now || !expires){
            if (!token || !expires) {
                localStorage.setItem('messageLogin', 'Faca o login para continuar');
            }else if(expires < now){
                localStorage.setItem('messageLogin', 'Sua sessao expirou');
            }
            window.location.href = '/';
        } 
    },[]);

    return(
        <div className="container-scroller">
            <Navbar />
            <div className="container-fluid page-body-wrapper pt-0 proBanner-padding-top">
                <Sidebar />
                <div className="main-panel">
                    <div className="content-wrapper">
                        <CardExtrato />
                        <Footer />
                    </div>
                </div>
            </div>
        </div>
    );
}